
.vt-radio {
	color: #606266;
	font-weight: 500;
	line-height: 1;
	position: relative;
	cursor: pointer;
	display: inline-block;
	// 文本不进行换行
	white-space: nowrap;
	outline: none;
	font-size: 14px;
	margin-right: 30px;
	-moz-user-select: none;
	-webkit-user-select: none;
	-moz-user-select: none;
	.vt-radio_input {
		white-space: nowrap;
		cursor: pointer;
		outline: none;
		display: inline-block;
		line-height: 1;
		position: relative;
		// 自身属性
		vertical-align: middle;
		.vt-radio_inner {
			border: 1px solid #dcdfe6;
			border-radius: 100%;
			width: 14px;
			height: 14px;
			background-color: #fff;
			position: relative;
			cursor: pointer;
			display: inline-block;
			box-sizing: border-box;
			// 中间的小白圆圈
			&:after {
				// 相对父元素居中
				width: 4px;
				height: 4px;
				border-radius: 100%;
				background-color: #fff;
				content: '';
				position: absolute;
				left: 50%;
				top: 50%;
				transform: translate(-50%, -50%) scale(0);
				transition: transform 0.15s ease-in;
			}
		}
		.vt-radio_original {
			opacity: 0;
			outline: none;
			position: absolute;
			z-index: -1;
			top: 0;
			left: 0;
			right: 0;
			bottom: 0;
			margin: 0;
		}
	}
	.vt-radio_label {
		font-size: 14px;
		padding-left: 10px;
	}
}
.vt-radio.is-checked {
	.vt-radio_input {
		.vt-radio_inner {
			border-color: #409eff;
			background-color: #409eff;
			&:after {
				// 显示小白点
				transform: translate(-50%, -50%) scale(1);
			}
		}
	}
	.vt-radio_label {
		color: #409eff;
	}
}
